import { baseRequest, normalRequest } from './base';

//api of self(user) management of self user and is different than the userManagement APIs

const createBackup = async (data, signal) => {
    try {
        return await baseRequest("api/v1/user/backup/create", "POST", data, signal);
    } catch (e) {
        alert(e);
    }
};

const getBackups = async (data, signal) => {
    try {
        return await baseRequest("api/v1/user/backups/get", "POST", data, signal);
    } catch (e) {
        alert(e);
    }
};

const downloadBackup = async (data, signal, todo) => {
    try {
        return await baseRequest("api/v1/user/backup/download", "POST", data, signal, todo);
    } catch (e) {
        alert(e);
    }
};


const restore = async (data,signal,  todo) => {
    try {
        return await baseRequest("api/v1/user/backup/restore", "POST", data, signal, todo);
    } catch (e) {
        alert(e);
    }
};



export {
    createBackup,
    getBackups,
    downloadBackup,
    restore,
}