import React from 'react';

import { Modal, Button, ProgressBar } from 'react-bootstrap';

const ProgressModal = (props) => {


    return (
        <Modal
            style={{ border: 'none', color: '#3d4075', boxShadow: '0 0 10px red' }}
            show={props.show}
            onHide={() => { }}
        >
            <Modal.Header style={{ margin: 'auto 5px' }}>
                <Modal.Title style={{ paddingLeft: '15px', color: '#3d4075', fontSize: '1.4em', fontWeight: 'bold' }}>Progress</Modal.Title>
            </Modal.Header>

            <Modal.Body
                style={{ paddingLeft: '30px' }}
            >
                <ProgressBar
                    className="m-t-20 m-b-30"
                    animated={props.data.value < 100 && props.data.value > 0 ? true : false}
                    now={props.data.value}
                />
                <div>{props.data.msg}</div>
            </Modal.Body>


            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end', padding: '10px' }}>

                <Button variant="primary" onClick={props.onHide}>
                    <b>Done</b>
                </Button>
            </div>
        </Modal >
    )
}

export default ProgressModal;