import React from 'react';
import { Row, Col, Card, Table, Accordion, Button, Collapse, Modal } from 'react-bootstrap';
import styled from "styled-components";
import PropTypes from 'prop-types';

import Aux from "../../hoc/_Aux";

import IsEmpty from '../../App/components/Reusables/IsEmpty';
import Loader from '../../App/components/Reusables/Loader';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import isEmpty from '../../validation/isEmpty';
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import { getTaxes, deleteTaxes } from '../../api/misc';
import { getTaxesSearchFilterObject } from '../../helpers/getSearchFilterObject';
import TaxesFilter from '../../App/components/SearchFilters/TaxesFilter';
import { getStartEnd } from '../../helpers/getStartEnd';
import { getBackups, createBackup, downloadBackup, restore } from '../../api/backups';
import { connect } from 'react-redux';
import ProgressModal from './ProgressModal';
import DEMO from '../../globals/constant';
import { logout } from '../../redux/actions/authActions';
import RestoreFromFileModal from './RestoreFromFileModal';

const IconButtonWrapper = styled.div`
  float: right;
  transform: rotate(0deg);
  overflow: hidden;
  transition: all 0.3s ease-out;
  transform: ${props => (props.rotate ? `rotate(90deg)` : "")};
`;





const Backups = (props) => {

    const [socket, setSocket] = React.useState(null)

    const [backupsObj, setBackupsObj] = React.useState([]);
    const [loading, setLoading] = React.useState(true)
    const [progressData, setProgressData] = React.useState({ showModal: false, data: {} });
    const [instanceCount, setInstanceCount] = React.useState(0);
    const [showingArr, setShowingArr] = React.useState([]);

    const [restoreFromFileModalShowing, setRestoreFromFileModalShowing] = React.useState(false);

    const [isRestored, setIsRestored] = React.useState(false);



    const getBackupsData_ = (signal) => {
        getBackups({
            "action": "get",
        }, signal)
            .then((res) => {
                console.log(res);
                if (res.json.status) {
                    setBackupsObj(res.json.backup_date);
                    setShowingArr((new Array(Object.keys(res.json.backup_date).length)).fill(false))
                    // setInstanceCount(res.json.count)
                    setLoading(false);
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch((err) => {
                console.log(err)
            });
    }

    React.useEffect(() => {
        const controller = new AbortController()



        getBackupsData_(controller.signal);

        const chatSocket = new WebSocket(
            `${DEMO.WEBSOCKET_URL}ws/backup/create/progress?token=` + props.auth.user.accessToken
        );
        setSocket(chatSocket);

        console.log(progressData)

        var timeoutId;
        chatSocket.onmessage = function (e) {
            const data = JSON.parse(e.data);
            console.log(data)
            if (data.message.value === 0) {
                setRestoreFromFileModalShowing(false);
            }

            setProgressData({ ...progressData, showModal: true, data: data.message });

            // document.querySelector('#log').value += (data.message.msg + ' ' + data.message.value + '\n');
        };

        chatSocket.onclose = function (e) {
            console.log('Chat socket is now disconnected');
        };

        return () => {
            controller.abort();
            clearTimeout(timeoutId);
            chatSocket.close();
        }
    }, []);

    React.useEffect(() => {
        var timeoutId;

        if (isRestored) {
            timeoutId = setTimeout(
                () => {
                    props.logout();
                }, 2000);


        }
        return () => {
            clearTimeout(timeoutId);
        }
    }, [isRestored])


    React.useState(() => {

    }, [socket])

    // console.log(progressData)

    // const _triggerDeleteModal = (id) => {
    //     setDeleteData({ showModal: true, taxId: id });
    // }

    const _toggleTimes = (index) => {
        const tempShowingArr = [...showingArr];
        tempShowingArr[index] = !tempShowingArr[index];
        setShowingArr(tempShowingArr);
    }


    const _backup = () => {
        console.log('proceed to backup');

        setProgressData({ ...progressData, showModal: true });

        createBackup({
            action: 'backup'
        })
            .then(res => {
                console.log(res, 'is the response here')
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Data backed up successfully!', background: '#dffff0' })
                    getBackupsData_();
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch(err => console.log(err))
    }

    const _downloadBackup = (date, time) => {
        console.log('download');
        downloadBackup({
            action: 'download',
            date,
            time,
        }, null, 'download')
            .then(res => {
                console.log(res);
                if (!res.json.status) Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
            })
            .catch(err => {
                console.log(err)
            })
    }


    const _restoreWithFile = () => {

    }

    const _restore = (date, time) => {
        const requestJson = {
            "action": "restore",
            "method": "selection",
            "date": date,
            "time": time,
        }
        restore(requestJson)
            .then((res) => {
                console.log(res);
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Restore is complete, Please re-login.', background: '#dffff0' })
                    setIsRestored(true);
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch(err => {
                console.log(err)
            })
    }




    const BackupsList = Object.keys(backupsObj).map((key, index) => {
        return (

            <div key={index}>
                <Modal.Header
                    onClick={() => _toggleTimes(index)}
                    style={{ margin: 'auto 5px', border: 'none', cursor: 'pointer', display: 'flex', 'justifyContent': 'flex-start', }}
                >
                    <Modal.Title
                        style={{ paddingLeft: '10px', color: '#3d4075', fontSize: '1.1em', fontWeight: 'bold', }}
                    >
                        {new Date(key).toDateString()}
                    </Modal.Title>

                    <Modal.Title
                        style={{ paddingLeft: '10px', color: '#3d4075', fontSize: '1.1em', fontWeight: 'bold', }}
                    >
                        <IconButtonWrapper rotate={showingArr[index] ? 1 : 0}>

                            <i className="feather icon-chevron-right text-c-white f-15" />
                        </IconButtonWrapper>
                    </Modal.Title>
                </Modal.Header>
                <Collapse in={!showingArr[index]}>
                    <div id="basic-collapse" className=" p-l-50" >
                        {
                            backupsObj[key].map((el, inx) => {
                                return <div key={inx} className='d-flex justify-content-start  p-1'>
                                    <div>{el}</div>
                                    <div
                                        className='p-l-10'
                                        onClick={() => _downloadBackup(key, el)}
                                        title="Download the backup of this date."
                                    >
                                        <i className="fa fa-download text-c-white f-15" style={{ cursor: 'pointer', color: '#3d4075' }} />
                                    </div>
                                    {/* <Button
                                        title="Restore the system using the backup done on this date"
                                        className="p-0 m-0 ml-2"
                                        variant="link"
                                        onClick={() => _restore(key, el)}
                                    >
                                        Restore
                                    </Button> */}

                                </div>
                            })
                        }
                    </div>
                </Collapse>
            </div>
        )
    })

    return (
        <Aux>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10">Backup</h4>
                <div>
                    {/* <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => {
                            setRestoreFromFileModalShowing(true)
                        }}
                    >
                        <i className="fa fa-cloud-upload text-c-white f-15 m-r-5" />
                        Restore from File
                    </button> */}

                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={_backup}
                    >
                        <i className="fa fa-database text-c-white f-15 m-r-5" />
                        Backup Now
                    </button>
                </div>
            </Row>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            {/* <TaxesFilter /> */}
                            {BackupsList}
                            {isEmpty(backupsObj) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /></> : ''}
                            {loading ? <Spinner /> : null}
                            <Paging instanceCount={instanceCount} />

                        </Card.Body>
                    </Card >
                    <ProgressModal
                        show={progressData.showModal}
                        onHide={() => setProgressData({ ...progressData, showModal: false, data: {} })}
                        data={progressData.data}
                    />
                    {/* <RestoreFromFileModal
                        show={restoreFromFileModalShowing}
                        onHide={() => setRestoreFromFileModalShowing(false)}
                        {...props}
                        setIsRestored={setIsRestored}
                    /> */}
                </Col>
            </Row>
        </Aux>
    );
}
Backups.propTypes = {
    auth: PropTypes.object.isRequired,
    logout: PropTypes.func.isRequired,

}


const mapStateToProps = state => ({
    auth: state.auth,
})



export default connect(mapStateToProps, { logout })(Backups);